<template>
    <div>
        <head-top webActive="home"></head-top>
        <div class="bgLogin" id="login">
            <div class="container">
                <div class="loginBg">
                    <div class="regBox">
                        <div class="login-panel">
                            <h2>绑定用户信息</h2>
                            <el-form :model="form" :rules="rules" ref="form" label-width="80px" class="mt15">
                                <el-form-item label="用户名" prop="username">
                                    <el-input type="text" v-model="form.username" placeholder="请输入用户名"></el-input>
                                </el-form-item>
                                <el-form-item label="密码" prop="password">
                                    <el-input type="password" v-model="form.password" placeholder="请输入密码" show-password></el-input>
                                </el-form-item>
                                <el-form-item label="确认密码" prop="checkpassword">
                                    <el-input type="password" v-model="form.checkpassword" placeholder="请再次输入密码" show-password></el-input>
                                </el-form-item>
                                <el-button type="primary" @click="submitForm('form')" class="w">确定</el-button>
                            </el-form>
                        </div>
                        <div class="loginP_bottom tc">已有账号？<router-link class="colorGreen" :to="{path:'/login/login',query:{}}">登录</router-link></div>
                    </div>
                </div>
            </div>
        </div>
    </div>

</template>


<script>
    import {writeUserInfo} from '../../service/api'
    import {setStore,getStore} from '../../config/mUtils'
    import headTop from '../../components/header/headTop';
    import {mapState, mapMutations, mapActions} from 'vuex'
    export default {
        name: 'userInfo',
        data(){
            return{
                form:{
                    username:'',
                    password:'',
                    checkpassword:'',
                    mobile:''
                },
                rules:{
                    username: [
                        { required: true, type: 'string', message: '请输入用户名',trigger: 'blur'}
                    ],
                    password:[
                        {required: true, type: 'string', message: '请输入密码', trigger: 'blur'}
                    ],
                    checkpassword:[
                        {required: true, type: 'string',message: '请再次输入密码' , trigger: 'blur'}
                    ]
                }
            }
        },
        components:{
            headTop
        },
        mounted(){
            this.initData();

        },
        computed:{
            ...mapState([
                'userInfo','login'
            ])
        },
        methods:{
            ...mapActions([
                'getUserInfo'
            ]),
            ...mapMutations([
                'RECORD_USERINFO',
            ]),
            async initData(){
                this.form.mobile = this.$route.query.mobile;
                console.log(this.form.mobile);
                if(!this.form.mobile){
                    var mobile = getStore('mobile');
                    this.form.mobile = mobile;
                }
            },
            submitForm(formName) {
                this.$refs[formName].validate(async (valid) => {
                    if (valid) {
                        if(this.form.password == this.form.checkpassword){
                            var res = await writeUserInfo(this.form.username,this.form.password,this.form.mobile);
                            if(res){
                                setStore('token', res.token);
                                this.$message({
                                    message: res.msg,
                                    type: res.status==12000?'success':'warning'
                                });
                                if(res.status==12000){
                                    this.getUserInfo();
                                    var self = this;
                                    setTimeout(function(){
                                        self.$router.push({path:"/home"});
                                    },1000);
                                }
                            }
                        }else{
                            this.$message({
                                message:'两次输入密码不一致',
                                type:'warning'
                            })
                        }
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
        },
        created(){

        },

    }

</script>

<style scoped>
    /*登录*/
    .bgLogin{
        background: -webkit-linear-gradient(30deg,#007991, #78ffd6); /* Safari 5.1 - 6.0 */
        background: -o-linear-gradient(30deg,#007991, #78ffd6); /* Opera 11.1 - 12.0 */
        background: -moz-linear-gradient(30deg,#007991, #78ffd6); /* Firefox 3.6 - 15 */
        background: linear-gradient(30deg,#007991, #78ffd6); /* 标准的语法 */
    }
    .loginBg{padding:80px 0;}
    .login-des{width: 100%;text-align: center;margin-right: 30px;flex-shrink: 1;}
    .login-des img{width:500px;margin:0 auto;}
    .loginBox{min-width: 420px;}
    .login-panel{background-color: #fff;border-radius: 10px 10px 0 0;padding:15px 30px;flex-shrink: 0;}
    .loginT{font-size: 16px;text-align: center;width:50%;line-height: 40px;cursor: pointer;}
    .loginT.active{border-bottom: 3px solid #0bb8b2;}
     .loginCodeBtn:hover,.loginBtn:hover{background-color:#0a9e9b;color:#fff;}
    .loginP_bottom{background-color: #ddd;color:#333;border-top:1px solid #eee;border-radius: 0 0 10px 10px;line-height: 60px;position: relative;font-size:14px;width:100%;
        height: 60px;}
    .loginP_bottom a:hover{text-decoration: underline;}

    /*注册*/
    .regBox{width: 500px;margin:0 auto;}
    .regBox h2,.regBox h3{font-size:28px;font-weight: bold;line-height: 36px;color:#0bb8b2;}
</style>



